<template>
  <div>
    <home-herder></home-herder>
     <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view">
      <md-scroll-view
        ref="scrollView"
        :scrolling-x="false"
        @scroll="$_onScroll"
        :autoReflow="true"
      >
        <home-banner></home-banner>
        <content-one></content-one>
        <recommend></recommend>
        <home-boutique></home-boutique>
      </md-scroll-view>
    </div>
  </div>
</template>

<script>

import homeHerder from './components/header'
import homeBanner from './components/banner'
import contentOne from './components/content-one'
import recommend from './components/recommend'
import homeBoutique from './components/boutique'

import {ScrollView} from 'mand-mobile'
export default {
  name: 'Home',
  components:{
    [ScrollView.name]: ScrollView,
    homeHerder,
    homeBanner,
    contentOne,
    recommend,
    homeBoutique
  },
  mounted() {
  },
  methods: {
    $_onScroll({scrollTop}) {
     console.log(scrollTop)
     if(scrollTop <= -60){
        return ;
     }
    }
  }
}
</script>

<style lang="stylus" scoped>
.md-example-child-scroll-view
    overflow: hidden
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0
</style>



